<template>
    <div class="search" @keyup.enter="search">
        <form-create v-model="formObj" :rule="rule" :option="options" @btn-search="search" @btn-reset="reset">
        </form-create>
        <div class="li">
            <el-button type="primary" @click="add">新 增</el-button>
            <!-- <el-button type="default" @click="down">导 出</el-button> -->
        </div>
    </div>
</template>

<script>
    import SearchInputs from "../components/SearchInputs.vue";
    //接口
    import {
        getKpList,
        getKcList,
    } from "@/api/archives.js";
    export default {
        extends: SearchInputs,
        props: {

        },
        data() {
            return {
                //表单生成规则
                options: {
                    form: {
                        labelPosition: 'left',
                        labelWidth: '0.95rem'
                    },
                    submitBtn: false,
                    resetBtn: false
                },
                rule: [{
                    type: 'input',
                    field: "wkOrg",
                    title: '用能单位：',
                    value: '',
                    props: {
                        placeholder: '请输入用能单位',
                    },
                    col: {
                        span: 6,
                    }
                }, {
                    type: 'AutoCompleteQuery',
                    field: 'kcCode',
                    title: '公司名称：',
                    props: {
                        placeholder: '请选择公司或输入名称搜索',
                        fieldValue: 'code',
                        fieldLabel: 'fullName',
                        value: '',
                        queryFunc: (keyWord) => {
                            const opt = {
                                pageNum: 1,
                                pageSize: 20,
                                _loading: false, //不显示加载中图标
                            }
                            if (keyWord !== '') {
                                opt.keyWord = keyWord;
                            }
                            return getKcList(opt).then(res => {
                                if (res.data) {
                                    const list = res.data.map(item => ({
                                        ...item,
                                        value: item.fullName,
                                    }))
                                    return list
                                } else {
                                    return []
                                }
                            })
                        }
                    },
                    emit: ['select'],
                    col: {
                        span: 8
                    }
                }, {
                    type: 'AutoCompleteQuery',
                    field: 'kpCode',
                    title: '平台名称：',
                    props: {
                        placeholder: '请选择平台或输入名称搜索',
                        fieldValue: 'code',
                        fieldLabel: 'name',
                        value: '',
                        queryFunc: (keyWord) => {
                            const opt = {
                                pageNum: 1,
                                pageSize: 20,
                                kcCode: this.formObj.getValue('kcCode'),
                                _loading: false, //不显示加载中图标
                            }
                            if (keyWord !== '') {
                                opt.keyWord = keyWord;
                            }
                            return getKpList(opt).then(res => {
                                if (res.data) {
                                    const list = res.data.map(item => ({
                                        ...item,
                                        value: item.name,
                                    }))
                                    return list
                                } else {
                                    return []
                                }
                            })
                        }
                    },
                    emit: ['select'],
                    col: {
                        span: 6
                    }
                }, {
                    type: 'SubmitBtns',
                    field: 'btn',
                    emit: ['search', 'reset'],
                    col: {
                        span: 4,
                    }
                }, {
                    type: 'select',
                    field: "wkTy",
                    title: '用电性质：',
                    value: '',
                    options: [{
                        value: '',
                        label: '全部',
                    }, {
                        value: '钻井',
                        label: '钻井',
                    }, {
                        value: '压裂',
                        label: '压裂',
                    }, {
                        value: '生产',
                        label: '生产',
                    }, {
                        value: '试气',
                        label: '试气',
                    }, {
                        value: '生活',
                        label: '生活',
                    }],
                    col: {
                        span: 6
                    }
                }, {
                    type: 'AutoCompleteQuery',
                    field: 'acCode',
                    title: '结算单位：',
                    props: {
                        placeholder: '请选择结算单位或输入名称搜索',
                        fieldValue: 'code',
                        fieldLabel: 'fullName',
                        value: '',
                        queryFunc: (keyWord) => {
                            const opt = {
                                pageNum: 1,
                                pageSize: 20,
                                _loading: false, //不显示加载中图标
                            }
                            if (keyWord !== '') {
                                opt.keyWord = keyWord;
                            }
                            return getKcList(opt).then(res => {
                                if (res.data) {
                                    const list = res.data.map(item => ({
                                        ...item,
                                        value: item.fullName,
                                    }))
                                    return list
                                } else {
                                    return []
                                }
                            })
                        }
                    },
                    emit: ['select'],
                    col: {
                        span: 8
                    }
                }, {
                    type: 'select',
                    field: "status",
                    title: '状态：',
                    value: 'runing',
                    options: [{
                        value: '',
                        label: '全部',
                    }, {
                        value: 'runing',
                        label: '在运',
                    }, {
                        value: 'done',
                        label: '离场',
                    }],
                    col: {
                        span: 6
                    }
                }]
            }
        },
        mounted() {
            this.search();
        },
        methods: {
            search() {
                const formData = this.formObj.formData();
                const opt = {
                    ...formData,
                }
                for (let key in opt) {
                    if (opt[key] == '' || opt[key] == undefined) delete opt[key];
                }
                this.$emit('searchHand', opt)
            },
            add() {
                this.$emit('add')
            }
        }
    }
</script>
<style lang="scss">
    .search {
        padding: 24px 0 0px 0;

        .el-form-item {
            margin-bottom: 0.16rem;
        }

        .li {
            display: flex;
            margin-bottom: 16px;
        }
    }
</style>