<!--用电申请-->
<template>
    <BoxTitle>
        <SearchInputs @searchHand="searchHand" @add="addHand"></SearchInputs>
        <Table :tableData="tableData" @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange"
            :pageNum="page.pageNum" @btnClick="btnClick" :pageSize="page.pageSize" :total="total">
        </Table>
        <!--弹窗编辑或新增-->
        <el-dialog :title="this.currEditRow=='new'?'新增用电申请':'用电单位离场'" custom-class="cusDialog search"
            :visible.sync="this.dialogShow" width="9rem" :before-close="handleClose">
            <form-create v-model="formObj" :rule="rule" :option="options" @jp-code-select="selectjP"
                @kc-code-select="selectKc" @kp-code-select="selectKp" @ac-code-select="selectAc">
            </form-create>
            <template v-if="this.currEditRow=='new'">
                <div class="content-title">请选择用电合同：<span v-if="this.currentRow&&this.currentRow.name"
                        class="selected-label">
                        已选择：{{this.currentRow.name}}
                        <el-button type="danger cancelBtn" size="small" @click="currentRowHander()">取消选择</el-button>
                    </span>
                </div>
                <div class="item">
                    <el-table v-table-scroll ref="searchTable" highlight-current-row row-key="index" max-height="300"
                        :data="searchRecordTableData" :header-cell-style="{background:'#E6EAFE',color:'#606266'}"
                        @current-change="currentRowHander" size="small">
                        <el-table-column label="序号" type="index" width="90" />
                        <el-table-column label="合同名称" prop="name" width="370" show-overflow-tooltip></el-table-column>
                        <el-table-column label="业主公司名称" prop="kcName" width="230" show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column label="用电性质" prop="wkTy" width="160" show-overflow-tooltip></el-table-column>
                        <el-table-column label="合同单价" prop="price" width="110"></el-table-column>
                        <el-table-column label="税率" prop="taxRate" width="90"></el-table-column>
                        <el-table-column label="合同编码" prop="code" width="230" show-overflow-tooltip></el-table-column>
                        <el-table-column label="运营类型" prop="devType" width="90"></el-table-column>
                        <span v-if="kcName" slot="empty">
                            该公司无合同记录，<span class='link' @click="goRouter">去这里添加</span>
                        </span>
                    </el-table>
                </div>
            </template>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" :loading="loading" @click="submit">确 定</el-button>
                <el-button @click="handleClose">关 闭</el-button>
            </span>
        </el-dialog>
        <!--关联合同管理-->
        <el-dialog title="关联合同查看" custom-class="cusDialog search" :visible.sync="this.dialogShowBindFinance"
            width="9rem" :before-close="closeHander">
            <div class="content-title">已绑定合同：</div>
            <div class="item">
                <el-table v-table-scroll row-key="index" max-height="300" :data="bindRecordTableData"
                    :header-cell-style="{background:'#E6EAFE',color:'#606266'}">
                    <el-table-column label="合同名称" prop="name" width="320" show-overflow-tooltip></el-table-column>
                    <el-table-column label="合同单价" prop="price" width="110"></el-table-column>
                    <el-table-column label="业主公司名称" prop="kcName" width="230" show-overflow-tooltip></el-table-column>
                    <el-table-column label="用电性质" prop="wkTy" width="160" show-overflow-tooltip></el-table-column>
                    <el-table-column label="税率" prop="taxRate" width="70"></el-table-column>
                    <el-table-column label="合同编码" prop="code" width="230" show-overflow-tooltip></el-table-column>
                    <el-table-column label="运营类型" prop="devType" width="90"></el-table-column>
                </el-table>
            </div>
            <template v-if="showEditBtn && !bindRecordTableData.length">
                <div v-if="!showAddContent">
                    <el-button type="primary" @click="showAddContent = true">新 增</el-button>
                    </el-button>
                </div>
                <div v-if="showAddContent">
                    <div class="item">
                        <form-create v-model="editRecordForm" :rule="editRecordRule" :option="editRecordOptions"
                            @btn-search="editRecordSearch" @btn-reset="editRecordReset">
                        </form-create>
                    </div>
                    <div class="item">
                        <el-table ref="editSearchTable" row-key="index" max-height="300"
                            :data="editSearchRecordTableData"
                            :header-cell-style="{background:'#E6EAFE',color:'#606266'}"
                            @current-change="editCurrentRowHander" highlight-current-row>
                            <el-table-column label="合同名称" prop="name" width="370" show-overflow-tooltip>
                            </el-table-column>
                            <el-table-column label="甲方公司" prop="kcName" width="230" show-overflow-tooltip>
                            </el-table-column>
                            <el-table-column label="乙方公司" prop="yfName" width="230" show-overflow-tooltip>
                            </el-table-column>
                            <el-table-column label="用电性质" prop="wkTy" width="160" show-overflow-tooltip>
                            </el-table-column>
                            <el-table-column label="合同单价" prop="price" width="110"></el-table-column>
                            <el-table-column label="税率" prop="taxRate" width="90"></el-table-column>
                            <el-table-column label="合同编码" prop="code" width="230" show-overflow-tooltip>
                            </el-table-column>
                            <el-table-column label="运营类型" prop="devType" width="90"></el-table-column>
                        </el-table>
                    </div>
                    <el-button type="primary" @click="editRecord" :disabled="!editCurrentRow.name">提 交
                    </el-button>
                    </el-button>
                    <el-button type="default" @click="cancelEditRecord">取 消</el-button>
                    </el-button>
                    <span v-show="editCurrentRow.name" class=" selected-label">已选择： {{editCurrentRow.name}}</span>
                </div>
            </template>
            <span slot="footer" class="dialog-footer">
                <el-button @click="closeHander">关 闭</el-button>
            </span>
        </el-dialog>
    </BoxTitle>
</template>
<script>
    //组件
    import BoxTitle from "@/components/BoxTitle.vue";
    import Index from "../components/Index.vue";
    import Table from "./Table.vue";
    import SearchInputs from "./SearchInputs.vue";
    //接口
    import {
        getJprocess,
        getKpList,
        getKcList,
        getPointsList,
        postJprocess,
        postWkout,
        getContractQuery,
        getContractQueryById,
        getConBind
    } from "@/api/archives.js";
    //公用函数
    import tool from "@/utils/tool.js";
    import {
        getDayStart
    } from "@/utils/index.js";

    export default {
        extends: Index, //继承通用index
        name: 'useEqManage',
        components: {
            BoxTitle,
            SearchInputs,
            Table
        },
        data() {
            return {
                options: {
                    form: {
                        labelPosition: 'left',
                        labelWidth: '100px'
                    },
                    submitBtn: false,
                    resetBtn: false
                },
                showEditBtn: false, //是否显示新增内容
                showAddContent: false, //新增绑定记录的界面
                editRecordOptions: { //编辑绑定时的配置
                    form: {
                        labelPosition: 'left',
                        labelWidth: '1.2rem'
                    },
                    submitBtn: false,
                    resetBtn: false
                },
                editRecordForm: {}, //编辑绑定时的配置 添加记录的搜索条件对象
                dialogShowBindFinance: false, //关联合同弹窗
                searchRecordTableData: [], //搜索结果的项
                editSearchRecordTableData: [], //绑定时搜索结果的项
                selectedRowsSearchRecord: [], //选择的项
                bindRecordTableData: [], //选择的项
                currentRow: {}, // 选择的一项
                editCurrentRow: {}, // 编辑时选择的一项合同

                showAdd: false,
                jpName: '', //计量点名称
                kcName: '', //公司名称
                rule: [],
                editRecordRule: [{
                    type: 'input',
                    field: "kcName",
                    title: '业主公司名称：',
                    value: '',
                    props: {
                        placeholder: '请输入业主公司名称',
                    },
                    validate: [{
                        validator: (rule, value, callback) => {
                            if (value === '') {
                                callback(new Error('名称不能为空'));
                            } else if (value.trim() == '') {
                                callback(new Error('请输入非空格字符'));
                            } else {
                                callback();
                            }
                        },
                        required: true,
                        trigger: 'blur'
                    }],
                    col: {
                        span: 16,
                    }
                }, {
                    type: 'SubmitBtns',
                    field: 'btn',
                    emit: ['search', 'reset'],
                    col: {
                        span: 6,

                    }
                }],
                editRule: [, {
                    type: "DatePicker",
                    field: "endTime",
                    title: "结束用电：",
                    value: getDayStart(),
                    props: {
                        type: "datetime",
                        format: "yyyy-MM-dd HH:mm:ss",
                        placeholder: "请选择日期",
                    },
                    validate: [{
                        required: true,
                        message: '请选择结束用电日期',
                        trigger: 'blur'
                    }],
                    col: {
                        span: 12
                    }
                }, {
                    type: "input",
                    field: "endDima",
                    title: "离场底码值：",
                    value: 0,
                    placeholder: '请输入离场底码',
                    validate: [{
                        required: true,
                        message: '请输入离场底码',
                        trigger: 'blur'
                    }],
                    col: {
                        span: 12
                    }
                }],
                AddRule: [{
                    type: 'AutoCompleteQuery',
                    field: 'kcCode',
                    title: '公司名称：',
                    props: {
                        placeholder: '请选择公司或输入名称搜索',
                        fieldValue: 'code',
                        fieldLabel: 'fullName',
                        value: '',
                        queryFunc: (keyWord) => {
                            const opt = {
                                pageNum: 1,
                                pageSize: 20,
                                _loading: false, //不显示加载中图标
                            }
                            if (keyWord !== '') {
                                opt.keyWord = keyWord;
                            }
                            return getKcList(opt).then(res => {
                                if (res.data) {
                                    const list = res.data.map(item => ({
                                        ...item,
                                        value: item.fullName,
                                    }))
                                    return list
                                } else {
                                    return []
                                }
                            })
                        }
                    },
                    validate: [{
                        required: true,
                        message: '请选择公司',
                        trigger: 'change'
                    }],
                    emit: ['select'],
                    col: {
                        span: 15
                    }
                }, {
                    type: 'AutoCompleteQuery',
                    field: 'kpCode',
                    title: '平台名称：',
                    props: {
                        placeholder: '请选择平台或输入名称搜索',
                        fieldValue: 'code',
                        fieldLabel: 'name',
                        value: '',
                        queryFunc: (keyWord) => {
                            const opt = {
                                pageNum: 1,
                                pageSize: 20,
                                kcCode: this.formObj.getValue('kcCode'),
                                _loading: false, //不显示加载中图标
                            }
                            if (keyWord !== '') {
                                opt.keyWord = keyWord;
                            }
                            return getKpList(opt).then(res => {
                                if (res.data) {
                                    const list = res.data.map(item => ({
                                        ...item,
                                        value: item.name,
                                    }))
                                    return list
                                } else {
                                    return []
                                }
                            })
                        }
                    },
                    validate: [{
                        required: true,
                        message: '请选择平台',
                        trigger: 'change'
                    }],
                    emit: ['select'],
                    col: {
                        span: 9
                    }
                }, {
                    type: 'AutoCompleteQuery',
                    field: 'jpCode',
                    title: '计量点：',
                    props: {
                        placeholder: '请选择计量点或输入名称搜索',
                        fieldValue: 'code',
                        fieldLabel: 'name',
                        queryFunc: (keyWord) => {
                            const opt = {
                                pageNum: 1,
                                pageSize: 20,
                                _loading: false,
                            }
                            if (keyWord !== '') {
                                opt.jpName = keyWord;
                            }
                            const {
                                kpCode,
                                kcCode,
                            } = this.formObj.formData();
                            if (kpCode !== '') {
                                opt.kpCode = kpCode;
                            }
                            if (kcCode !== '') {
                                opt.kcCode = kcCode;
                            }
                            return getPointsList(opt).then(res => {
                                if (res.list) {
                                    const list = res.list.rows.map(item => ({
                                        ...item,
                                        value: item.name
                                    }))
                                    return list
                                } else {
                                    return []
                                }
                            })
                        }
                    },
                    validate: [{
                        required: true,
                        message: '请选择计量点',
                        trigger: 'change'
                    }],
                    emit: ['select'],
                    col: {
                        span: 15
                    }
                }, {
                    type: 'select',
                    field: "wkTy",
                    title: '用电性质：',
                    value: '钻井',
                    options: [{
                        value: '钻井',
                        label: '钻井',
                    }, {
                        value: '压裂',
                        label: '压裂',
                    }, {
                        value: '生产',
                        label: '生产',
                    }, {
                        value: '试气',
                        label: '试气',
                    }, {
                        value: '生活',
                        label: '生活',
                    }],
                    validate: [{
                        required: true,
                        message: '请选择用电性质',
                        trigger: 'change'
                    }],
                    col: {
                        span: 9
                    }
                }, {
                    type: 'input',
                    field: "wkOrg",
                    title: '用能单位：',
                    value: '',
                    props: {
                        placeholder: '请输入用能单位',
                    },
                    validate: [{
                        required: true,
                        message: '请输入用能单位',
                        trigger: 'blur'
                    }],
                    col: {
                        span: 15,
                    }
                }, {
                    type: 'MyInputNumber',
                    field: "billDay",
                    title: '账单日：',
                    value: 25,
                    props: {
                        placeholder: '请输入1-31的数字',
                        append: '号',
                    },
                    validate: [{
                        required: true,
                        validator: (rule, value, callback) => {
                            if (value === '') {
                                callback(new Error('请输入账单日'));
                            } else if (value < 1 || value > 31) {
                                callback(new Error('请输入1-31的数字'));
                            } else {
                                callback();
                            }
                        },
                        trigger: 'blur'
                    }],
                    col: {
                        span: 9
                    }
                }, {
                    type: 'AutoCompleteQuery',
                    field: 'acCode',
                    title: '结算单位：',
                    props: {
                        placeholder: '请选择单位或输入名称搜索',
                        fieldValue: 'code',
                        fieldLabel: 'fullName',
                        value: '',
                        queryFunc: (keyWord) => {
                            const opt = {
                                pageNum: 1,
                                pageSize: 20,
                                _loading: false, //不显示加载中图标
                            }
                            if (keyWord !== '') {
                                opt.keyWord = keyWord;
                            }
                            return getKcList(opt).then(res => {
                                if (res.data) {
                                    const list = res.data.map(item => ({
                                        ...item,
                                        value: item.fullName,
                                    }))
                                    return list
                                } else {
                                    return []
                                }
                            })
                        }
                    },
                    validate: [{
                        required: true,
                        message: '请选择公司',
                        trigger: 'change'
                    }],
                    emit: ['select'],
                    col: {
                        span: 15
                    }
                }, {
                    type: "input",
                    field: "startDima",
                    title: "起始底码：",
                    value: 0,
                    placeholder: '请输入起始底码',
                    validate: [{
                        validator: (rule, value, callback) => {
                            if (value === '') {
                                callback(new Error('起始底码不能为空'));
                            } else if (value != Number(value)) {
                                callback(new Error('请输入合法数字'));
                            } else {
                                callback();
                            }
                        },
                        trigger: 'blur',
                        required: true,
                    }],
                    col: {
                        span: 9
                    }
                }, {
                    type: "DatePicker",
                    field: "startTime",
                    title: "开始用电：",
                    value: new Date(),
                    props: {
                        type: "datetime",
                        format: "yyyy-MM-dd HH:mm:ss",
                        placeholder: "请选择日期",
                    },
                    validate: [{
                        required: true,
                        message: '请选择开始用电日期',
                        trigger: 'blur'
                    }],
                    col: {
                        span: 15
                    }
                }],
            }
        },
        methods: {
            //操作按钮点击事件
            btnClick(btnName, row) {
                this.currEditRow = row; // 记录当前更改的数据
                if (btnName == '离场') { //用电单位离场
                    this.dialogShow = true;
                    if (this.rule !== this.editRule) {
                        this.rule = this.editRule;
                    }
                    this.$nextTick(() => {
                        this.formObj.reload();
                        this.formObj.resetFields();
                        //清除新增时查询的合同记录
                        this.currentRow = {};
                        this.$refs.searchTable.setCurrentRow();
                        this.searchRecordTableData = [];
                    })
                } else if (btnName == '合同查看') {
                    this.dialogShowBindFinance = true;

                    //查询合同
                    this._getContrastByid(this.currEditRow.conId);
                }
            },
            /**
             * 通过id查询合同
             */
            _getContrastByid(conId) {
                getContractQueryById({
                    conId
                }).then(res => {
                    const data = res.data ? [res.data] : [];
                    this.bindRecordTableData = data;
                    if (!data.length) {
                        this.showEditBtn = true;
                    }
                })
            },
            //默认获取表格数据和搜索数据
            _getData(params = {}) { // 通过接口 获取最新数据 如果无接口 则是第一次加载
                getJprocess({
                    ...this.page,
                    ...this.search
                }).then(res => {
                    this.tableData = res.rows;
                    this.total = res.total;
                })
            },
            //根据计量点名称查询用电记录
            editRecordSearch() {
                this.editRecordForm.validate((valid, fail) => {
                    if (valid) {
                        this._searchRecordAjax();
                    }
                })
            },
            //根据搜索条件查询
            _searchRecordAjax() {
                if (!this.editRecordForm.formData().kcName || this.editRecordForm.formData().kcName.trim() == '') {
                    return;
                }
                getContractQuery({
                    kcName: this.editRecordForm.formData().kcName,
                }).then(res => {
                    this.editSearchRecordTableData = res.rows ? res.rows : [];
                })
            },
            //重置搜索表单和记录
            editRecordReset() {
                this.editRecordForm.resetFields && this.editRecordForm.resetFields();
                this.editSearchRecordTableData = [];
                this.editCurrentRow = {};
            },
            goRouter() {
                this.dialogShow = false;
                this.$router.push("/archives");
            },
            //搜索按钮
            searchHand(data) {
                this.page.pageNum = 1; //搜索时页数置为1
                this.search = data;
                this._getData();
            }, //操作按钮点击的事件
            //新增按钮
            //根据计量点名称查询用电记录
            //根据搜索条件查询
            searchRecordAjax(kcName) {
                getContractQuery({
                    kcName,
                    pageSize: 100,
                }).then(res => {
                    this.currentRow = {};
                    this.$refs.searchTable.setCurrentRow();
                    this.searchRecordTableData = res.rows ? res.rows : [];
                })
            },
            //选择搜索记录
            currentRowHander(val) {
                if (!val) {
                    this.currentRow = {};
                    this.$refs.searchTable.setCurrentRow();
                } else {
                    this.currentRow = val;
                }
            },
            //选择搜索记录
            editCurrentRowHander(val) {
                if (!val) {
                    this.editCurrentRow = {};
                    this.$refs.editSearchTable.setCurrentRow();
                } else {
                    this.editCurrentRow = val;
                }
            },
            addHand() {
                this.currEditRow = 'new'; // 记录当前更改的数据
                this.dialogShow = true;
                if (this.rule.length !== this.AddRule.length) {
                    this.rule = this.AddRule;
                    this.$nextTick(() => {
                        this.formObj.reload();
                        this.formObj.resetFields();
                    })
                }
            },
            //计量点选择
            selectjP(value, item) {
                this.jpName = item.name;
                this.jpVersion = item.version;
            },
            //公司选择
            selectKc(value, item) {
                this.kcName = item.value;
                this.formObj.exec('kpCode', 'clear')
                this.formObj.exec('jpCode', 'clear')
                this.searchRecordAjax(item.value);
            },
            //平台选择
            selectKp(value, item) {
                this.kpName = item.value;
                this.formObj.exec('jpCode', 'clear')
            },
            //结算单位
            selectAc(value, item) {
                this.acName = item.value;
            },
            addClick() {
                this.showAdd = true;
            },
            cancel() {
                this.showAdd = false;
            },
            //关闭合同查看弹窗
            closeHander() {
                this.dialogShowBindFinance = false;
                this.bindRecordTableData = [];
                this.editCurrentRow = {};
                this.showEditBtn = false; //隐藏新增按钮
            },

            // 重置编辑时的表单
            cancelEditRecord() {
                this.showAddContent = false;
                this.editRecordReset();
            },
            //编辑时新增合同
            editRecord() {
                this.loading = true; //加载
                getConBind({
                    jprId: this.currEditRow.id,
                    conId: this.editCurrentRow.id
                }).then(res => {
                    this.loading = false; //加载 
                    this.$message.success('操作成功');
                    this.dialogShowBindFinance = false; // 关闭弹窗
                    //查询合同
                    this.editCurrentRow = {};
                    this.reloadTable();
                }).catch(() => {
                    this.loading = false; //加载
                })
            },
            //新增或编辑提交
            submit() {
                this.formObj.validate((valid, fail) => {
                    if (valid) {
                        const data = this.formObj.formData();
                        if (this.currEditRow == 'new') { //新增用电
                            this.loading = true; //加载
                            postJprocess({
                                ...data,
                                kcName: this.kcName,
                                kpName: this.kpName,
                                jpName: this.jpName,
                                acName: this.acName,
                                conId: this.currentRow.id, //合同id
                                jpVersion: this.jpVersion,
                                status: 'runing',
                            }).then(this._success.bind(this)).catch(this._err.bind(this));
                        } else { //离场
                            this.loading = true; //加载
                            postWkout({
                                ...data,
                                id: this.currEditRow.id,
                            }).then(this._success.bind(this)).catch(this._err.bind(this));
                        }
                    }
                })
            },
            //成功回调处理
            _success(res) {
                this.$message.success('操作成功');
                this.dialogShow = false;
                this.loading = false; //加载
                //清除新增时查询的合同记录
                this.currentRow = {};
                this.$refs.searchTable.setCurrentRow();
                this.searchRecordTableData = [];
                this.kcName = '';
                this.reloadTable();
                this._resetFields(); //重置表单
            },
            //错误回调处理
            _err(err) {
                this.loading = false; //加载
            },
            _resetFields() {
                this.formObj.resetFields();
                this.formObj.reload();
                this.formObj.exec('kcCode', 'setLabel', '')
                this.formObj.exec('kpCode', 'setLabel', '')
                this.formObj.exec('jpCode', 'setLabel', '')
                setTimeout(() => {
                    this.formObj.clearValidateState();
                }, 0);
            },
            //重写关闭弹窗
            handleClose() {
                this.dialogShow = false;
                this.showAdd = false;
            }
        }
    }
</script>
